html, body {
	margin:0;
	padding:0;
}

body {
	background:#F6F7F8;
}

.wave {
	position:relative;
	width:100px;
	margin-left: auto;
    margin-right: auto;
}
.dot1 {
    display:inline-block;
    width:12px;
    height:12px;
    border-radius:50%;
    margin-right:3px;
    background:white;
    -webkit-animation: wave 1.3s linear infinite;
            animation: wave 1.3s linear infinite;	
}

.dot2{
    display:inline-block;
    width:12px;
    height:12px;
    border-radius:50%;
    margin-right:3px;
    background:white;
    -webkit-animation: wave 1.3s linear infinite;
            animation: wave 1.3s linear infinite;	
    -webkit-animation-delay: -1.1s;	
            animation-delay: -1.1s; 
}

.dot3{
    display:inline-block;
    width:12px;
    height:12px;
    border-radius:50%;
    margin-right:3px;
    background:white;
    -webkit-animation: wave 1.3s linear infinite;
            animation: wave 1.3s linear infinite;	
    -webkit-animation-delay: -0.9s;	
            animation-delay: -0.9s; 
}

@-webkit-keyframes wave {
	0%, 60%, 100% {
		-webkit-transform: none;
		        transform: none;
		-webkit-transform: initial;
		        transform: initial;
	}
    
	30% {
		-webkit-transform: translateY(-15px);
		        transform: translateY(-15px);
	}
}

@keyframes wave {
	0%, 60%, 100% {
		-webkit-transform: none;
		        transform: none;
		-webkit-transform: initial;
		        transform: initial;
	}
    
	30% {
		-webkit-transform: translateY(-15px);
		        transform: translateY(-15px);
	}
}
